// LabelEditable.js
import React from 'react';

/**
 * Label과 함께 입력 필드를 렌더링하는 컴포넌트
 * @param {string} label - 입력 필드의 레이블
 * @param {string} type - 입력 필드의 타입 (예: 'email', 'password')
 * @param {string} value - 입력 필드의 값
 * @param {function} onChange - 입력 필드 변경 이벤트 핸들러
 * @returns {JSX.Element} 레이블과 입력 필드 JSX 컴포넌트
 */
function LabelEditable({ className, label, type, value, onChange }) {
  return (
    <div className={`ez-labeleditable-${className ? className : '' }`}>
      <label>{label}</label>
      <input type={type} value={value} onChange={onChange} />
    </div>
  );
}

export default LabelEditable;
