// ErrorMessage.js
import React from 'react';

/**
 * 에러 메시지를 표시하는 컴포넌트
 * @param {string} message - 표시할 에러 메시지 (빈 문자열 또는 null일 경우 표시 안 함)
 * @returns {JSX.Element | null} 에러 메시지 JSX 컴포넌트 또는 null
 */
function ErrorMessage({ message }) {
  return <div style={{ color: 'red' }}>{message ? message : ''}</div>;
}

export default ErrorMessage;
