/**
 * id, label, value 를 받아서 CheckedItem 을 만든다.
 * onDelete (id) => {} 삭제 아이콘을 클릭했을 때 호출되는 함수, id 를 parameter 로 넘긴다.
 * @param { id, label, value onDelete } param
 * @returns JSX.Element
 */
export const CheckedItem = ({ id, label, value, onDelete }) => {
  return (
    <div className="ez-checkeditem">
      {label}
      <span className="deleteIcon" onClick={() => onDelete(id)}>
        {' '}
        X{' '}
      </span>
    </div>
  );
};

/**
 * items 와 onDelete 함수를 받아서 CheckedList 를 만들어준다.
 * items 는 { id, label, value } 의 배열이다.
 * items 가 없으면 "선택된 항목이 없습니다." 라는 문구를 보여준다.
 * @param { items { id, label, value }[], onDelete: (idDeleted) => {} } param0 
 * @returns JSX.Element
 */
export const CheckedList = ({ items, onDelete }) => {
  if (items.length === 0) {
    return <div className="checked-list">선택된 항목이 없습니다.</div>;
  }

  return (
    <div className="ez-checkedlist">
      {items.map(item => (
        <CheckedItem
          key={item.id}
          id={item.id}
          label={item.label}
          onDelete={onDelete}
        />
      ))}
    </div>
  );
};
