import { useMemo, useState } from 'react';
import { isNil, includes, reject } from 'ramda';
import { to2DArrary } from 'ezy-fn';
import './ToggleButton.css';

/**
 * ToggleButton 의 사이즈를 결정하는 상수이다.
 */
export const TOGGLE_SIZE = {
  COMMON: 'common',
  SMALL: 'small',
};

/**
 * 선택시 active 되는 toggle button 을 만들어준다.
 * size 가 small 이면 작은 사이즈로 만들어준다. default 는 common 이다.
 * @param { id, label, value, onClick, isChecked, size: TOGGLE_SIZE } param
 * @returns JSX.Element
 */
export const ToggleButton = ({
  id,
  label,
  value,
  onClick,
  isChecked,
  size,
}) => {
  function handleClick() {
    const newIsChecked = !isChecked;
    onClick(newIsChecked, id, value);
  }

  return (
    <button
      className={`ez-btntoggle ${isChecked ? 'active' : ''} ${size === TOGGLE_SIZE.SMALL ? size : ''}`}
      onClick={handleClick}
    >
      {label}
    </button>
  );
};
export default ToggleButton;

/**
 * item: { id, label, value };
 * items 중에서 선택된 것은 itemsChecked 로 받아서 ToggleButton 을 만들어준다.
 * rowCount 는  toggleButton 을 row 당 몇개씩 보여줄 것인지 결정한다.
 * onClick 은 toggleButton 이 클릭되었을 때 호출되는 함수이다. 현재 체크된 items 를 parameter 로 받는 함수이다.
 * @param { items: <item>[], rowCount, onClick:(newItemsChecked) => {}, itemsChecked: <item>[] } param0
 * @returns JSX.Element
 */
export const ToggleButtonGroup = ({
  items,
  rowCount,
  onClick,
  itemsChecked,
  size,
}) => {
  // to2DArray 구현

  /**
   * items 를 2차원 배열로 만든다.
   * toggle button 을 row 당 몇개씩 보여줄 것인지 결정한다.
   * 한줄에 rowCount 만큼 보여준다.
   *
   * 예를 들어 [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }, { id: 5 }, { id:6 }] 이렇게 6개의 item 이 있다고 가정하자.
   * rowCount 가 2 이면 2차원 배열로
   *  => [ [{ id: 1 }, { id: 2 }], [{ id: 3 }, { id: 4 }], [{ id: 5 }, { id: 6 }] ]
   * rowCount 가 3 이면 2차원 배열로
   * => [ [{ id: 1 }, { id: 2 }, { id: 3 }], [{ id: 4 }, { id: 5 }, { id: 6 }] ]
   *
   * reduce 는 ramda 에서 제공하는 함수로서 배열을 하나의 값으로 축약한다.
   * https://ramdajs.com/docs/#reduce
   */
  const items2DArray = useMemo(() => to2DArrary(rowCount, items), [items]);

  const handleOnToggleClick = (isChecked, itemChecked) => {
    // reject 는 ramda 에서 제공하는 함수로서 배열에서 조건에 맞는 것을 제거한다.
    // https://ramdajs.com/docs/#reject
    // isChecked 가 true 이면 itemsChecked 에 itemChecked 를 추가하고
    // false 이면 itemsChecked 에서 itemChecked 를 제거하는 새로운 itemsChecked 를 만들어서 onClick 함수를 호출하여 넘긴다.
    const newItemsChecked = isChecked
      ? [...itemsChecked, itemChecked]
      : reject(el => el.id === itemChecked.id, itemsChecked);

    onClick(newItemsChecked);
  };

  /**
   * includes 는 ramda 에서 제공하는 함수로서 배열에 해당 값이 있는지 확인한다.
   * https://ramdajs.com/docs/#includes
   * itemsChecked 에서 id 를 비교하여 해당 item 이 선택되었는지 확인하고 ToggleButton 의 isChecked 를 결정한다.
   * @param {*} id
   * @param {*} itemsChecked
   * @returns boolean
   */
  const checkIsChecked = (id, itemsChecked) => {
    const idsChecked = itemsChecked.map(item => item.id);
    return includes(id, idsChecked);
  };

  return (
    <>
      {items2DArray.map((row, idx) => (
        <div key={idx} data-testid={`row${idx}`} className="ez-btnbunchrow">
          {row.map(item => (
            <ToggleButton
              key={item.id}
              id={item.id}
              label={item.label}
              value={item.value}
              isChecked={checkIsChecked(item.id, itemsChecked)}
              onClick={isChecked => handleOnToggleClick(isChecked, item)}
              size
            />
          ))}
        </div>
      ))}
    </>
  );
};

export const ToggleOneButtonGroup = ({
  items,
  rowCount,
  onClick,
  itemChecked,
  size,
}) => {
  const items2DArray = useMemo(() => to2DArrary(rowCount, items), [items]);

  const handleOnToggleClick = (isChecked, newItemChecked) => {
    if (isChecked) {
      onClick(newItemChecked);
    } else {
      onClick(undefined);
    }
  };

  const checkIsChecked = id => {
    if (isNil(itemChecked)) return false;

    if (id === itemChecked.id) return true;

    return false;
  };

  return (
    <>
      {items2DArray.map((row, idx) => (
        <div key={idx} data-testid={`row${idx}`} className="ez-btnbunchrow">
          {row.map(item => (
            <ToggleButton
              key={item.id}
              id={item.id}
              label={item.label}
              value={item.value}
              isChecked={checkIsChecked(item.id)}
              onClick={isChecked => handleOnToggleClick(isChecked, item)}
              size={size}
            />
          ))}
        </div>
      ))}
    </>
  );
};

export const useToggleOneButtonGroup = ({ items, defineOnChecked }) => {
  const [itemChecked, setItemChecked] = useState(undefined);

  return {
    btnItems: items,
    itemChecked,
    handleOnClick: newItemChecked => {
      setItemChecked(newItemChecked);
      defineOnChecked && defineOnChecked(newItemChecked);
    },
  };
};
